.profile-picture {
  margin-top: 50px;
}

.profile-picture img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 5px solid #fff; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
  transition: transform 0.3s ease-in-out;
}

.profile-picture img:hover {
  transform: scale(1.1);
}
